import { mapGetters } from 'vuex';
import EmptyContent from '../components/EmptyContent';
import File from '../components/File';
import FilesListViewer from '../components/FilesListViewer';
import { isMobile } from '@nextcloud/vue';
import SemaphoreWithPriority from '../utils/semaphoreWithPriority';
import FilesSelectionMixin from '../mixins/FilesSelectionMixin';
import Actions from '@nextcloud/vue/dist/Components/Actions';
import ActionButton from '@nextcloud/vue/dist/Components/ActionButton';
import ArrowLeft from 'vue-material-design-icons/ArrowLeft';
import AbortControllerMixin from '../mixins/AbortControllerMixin';
export default {
  name: 'TagContent',
  components: {
    File,
    FilesListViewer,
    EmptyContent,
    Actions,
    ActionButton,
    ArrowLeft
  },
  mixins: [isMobile, FilesSelectionMixin, AbortControllerMixin],
  props: {
    path: {
      type: String,
      default: ''
    }
  },

  data() {
    return {
      error: null,
      loading: false,
      semaphore: new SemaphoreWithPriority(30)
    };
  },

  computed: { // global lists
    ...mapGetters(['files', 'tags', 'tagsNames']),

    // current tag id from current path
    tagId() {
      return this.$store.getters.tagId(this.path);
    },

    // current tag
    tag() {
      return this.tags[this.tagId];
    },

    // files list of the current tag
    fileIds() {
      return this.tag ? this.tag.files : [];
    },

    isEmpty() {
      return this.fileIds.length === 0;
    }

  },
  watch: {
    async path() {
      this.fetchContent();
    }

  },

  async beforeMount() {
    this.fetchContent();
  },

  methods: {
    async fetchContent() {
      // close any potential opened viewer
      OCA.Viewer.close(); // if we don't already have some cached data let's show a loader

      if (!this.tags[this.tagId]) {
        this.loading = true;
        await this.$store.dispatch('fetchAllTags', {
          signal: this.abortController.signal
        });
      }

      this.error = null;

      try {
        await this.$store.dispatch('fetchTagFiles', {
          id: this.tagId,
          signal: this.abortController.signal
        });
      } catch (error) {
        console.error(error);
        this.error = true;
      } finally {
        // done loading
        this.loading = false;
      }
    },

    openViewer(fileId) {
      const file = this.files[fileId];
      OCA.Viewer.open({
        path: file.filename,
        list: this.fileIds.map(fileId => this.files[fileId]),
        loadMore: file.loadMore ? async () => await file.loadMore(true) : () => [],
        canLoop: file.canLoop
      });
    }

  }
};